import { useEffect, useState } from "react";
import "./App.css";
import NavigateMenu from "./component/NavigateMenu/NavigateMenu";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import ExampleSource from "./screens/ExampleSource/ExampleSource";
import Footer from "./component/Footer/Footer";
import About from "./screens/About/About";
import Product from "./screens/Product/Product";
import Contact from "./screens/Contact/Contact";
import News from "./screens/News/News";
import Home from "./screens/Home/Home";
import CartDetail from "./screens/CartDetail/CartDetail";
import "./assets/scss/common.scss";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
//dynamic icon

import { library } from "@fortawesome/fontawesome-svg-core";
import * as Icons from "@fortawesome/free-solid-svg-icons";

import { Provider } from "react-redux";
import store from "./store";

const iconList = Object.keys(Icons)
  .filter((key) => key !== "fas" && key !== "prefix")
  .map((icon) => Icons[icon]);
library.add(...iconList);

const listCategory = [
  {
    id: 1,
    label: "Máy ảnh &amp; Máy quay phim",
    subCategory: [
      {
        id: 2,
        label: "MÁY QUAY PHIM",
        subCategory: [
          { id: 4, label: "MÁY QUAY PHIM", subCategory: [] },
          { id: 5, label: "MÁY QUAY PHIM", subCategory: [] },
        ],
      },
      {
        id: 3,
        label: "Dien thoai",
        subCategory: [
          { id: 6, label: "MÁY QUAY PHIM", subCategory: [] },
          { id: 7, label: "MÁY QUAY PHIM", subCategory: [] },
        ],
      },
      {
        id: 4,
        label: "Dien thoai 1",
        subCategory: [
          { id: 8, label: "MÁY QUAY PHIM", subCategory: [] },
          { id: 9, label: "MÁY QUAY PHIM", subCategory: [] },
        ],
      },
    ],
  },
  {
    id: 2,
    label: "Máy ảnh",
    subCategory: [],
  },
];
function App() {
  const [listMenu, setListMenu] = useState([]);
  const getAllMenu = async () => {
    try {
      const header = {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      };
      const urlRequest = "./json/menu.json";
      const response = await fetch(urlRequest, header);
      const responseJson = await response.json();
      return responseJson;
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getAllMenu().then((value) => {
      setListMenu(value);
    });
  }, []);

  return (
    <Provider store={store}>
      <Router>
        <div className="App">
          {/* //get all icon name */}
          {/* {iconList.map(icon=>{
          return (
            <FontAwesomeIcon style={{padding:'5px'}} icon={icon.iconName}>
              {icon.iconName}
            </FontAwesomeIcon>
          );
        })} */}
          <NavigateMenu
            listMenu={listMenu}
          ></NavigateMenu>

          <Switch>
            <Route path="/about">
              <About />
            </Route>
            <Route path="/product">
              <Product />
            </Route>
            <Route path="/contact">
              <Contact />
            </Route>
            <Route path="/news">
              <News />
            </Route>
            <Route path="/" exact={true}>
              <Home />
            </Route>
            <Route path="/source">
              <ExampleSource />
            </Route>
            <Route path="/cart-detail">
              <CartDetail />
            </Route>
          </Switch>
          <Footer />
        </div>
      </Router>
    </Provider>
  );
}

export default App;
