import React, { useState } from "react";
import PropTypes from "prop-types";
import Steper from "../../component/Steper/Steper";
import Table from "../../component/Table/Table";
import { ACTION_TYPE_TABLE } from "../../Constant/Constant";
import NavigateQuickly from "../../component/NavigateQuickly/NavigateQuickly";
import Header from "../../component/Header/Header";
import Payment from "../../component/Payment/Payment";
import CompletedOrder from "../../component/CompletedOrder/CompletedOrder";

import { Button } from "@material-ui/core";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useRouteMatch,
} from "react-router-dom";
import { useHistory } from "react-router-dom";
import "./CartDetail.scss";
CartDetail.propTypes = {};

function CartDetail(props) {
  const [total, setTotal] = useState(() => {
    let total = 0;
    dataSourceTable.forEach((item) => {
      total += item.intoMoney;
    });
    return total;
  });

  const [listNavQuickly, setListNavQuickly] = useState(listQuickLink);
  const [listSteper, setListSteper] = useState(dataSourceSteper);
  const [dataList, setDataList] = useState(dataSourceTable);
  let { path, url } = useRouteMatch(); //get root path
  const history = useHistory(); //navigate next page

  const hanldeCalculateValue = (newValue, item, propertyName) => {
    if (propertyName === "amount") {
      item[propertyName] = newValue;
      item.intoMoney = newValue * item.price;
      dataSourceTable[item.index] = item;
      calculateTotal(dataSourceTable);
      setDataList([...dataSourceTable]);
    }
  };

  const calculateTotal = (listProduct) => {
    let total = 0;
    listProduct.forEach((item) => {
      total += item.intoMoney;
    });
    setTotal(total);
  };

  const hanldeEventDelete = (item, actionName) => {
    if (actionName === ACTION_TYPE_TABLE.DELETE) {
      dataSourceTable.splice(item.index, 1);
      calculateTotal(dataSourceTable);
      setDataList([...dataSourceTable]);
    }
  };

  const nextSteper = () => {};
  return (
    <div className="CartDetail">
      <div className="ContainerBody">
        <div className="NavQuickly">
          <NavigateQuickly listQuickLink={listNavQuickly} />
        </div>
        <div className="Header">
          <Header title="Thanh toán" />
        </div>
        <div className="Steper">
          <Steper listSteper={listSteper} />
        </div>
        <div className="ProductInfo">
          <Router>
            <Switch>
              <Route path="/cart-detail" exact={true}>
                <Table
                  dataList={dataList}
                  headerList={headerTable}
                  hanldeCalculateValue={hanldeCalculateValue}
                  hanldeAction={hanldeEventDelete}
                />
                <div className="Total">
                  <span className="Label">Tổng thanh toán:</span>
                  <span className="Content">
                    {total} <sup>đ</sup>
                  </span>
                </div>
              </Route>
              <Route path={`${path}/payment`}>
                <Payment />
              </Route>
              <Route path={`${path}/completed`}>
                <CompletedOrder/>
              </Route>
            </Switch>
          </Router>
        </div>

        <div className="ButtonSend">
          <Button className="ButtonLeft">Tiếp tục mua hàng</Button>
          <Button className="ButtonRight" onClick={() => nextSteper()}>
            Tiến hành thanh toán
          </Button>
        </div>
      </div>
    </div>
  );
}

export default CartDetail;

let listQuickLink = [
  {
    id: "1",
    label: "Trang chủ",
    path: "/",
  },
  {
    id: "2",
    label: "Giỏ hàng",
    path: "#",
  },
];
let dataSourceSteper = [
  {
    isActive: true,
    iconName: "angle-double-down",
    title: "Giỏ hàng",
  },
  {
    isActive: false,
    iconName: "list",
    title: "Thanh toán",
  },
  {
    isActive: false,
    iconName: "list",
    title: "Hoàn tất",
  },
];

const headerTable = [
  {
    flex: 1,
    label: "",
    propertyMapping: "imageUrl",
    type: "IMAGE",
  },
  {
    flex: 5,
    label: "TÊN SẢN PHẨM",
    propertyMapping: "name",
    type: "LABLE",
  },
  {
    flex: 2,
    label: "GIÁ",
    propertyMapping: "price",
    type: "LABLE",
  },
  {
    flex: 2,
    label: "SỐ LƯỢNG",
    propertyMapping: "amount",
    type: "INPUTNUMBER",
    min: 1,
    max: 10,
  },
  {
    flex: 2,
    label: "THÀNH TIỀN",
    propertyMapping: "intoMoney",
    type: "LABLE",
  },
  {
    flex: 1,
    label: "",
    actionType: ["DELETE"],
    type: "ACTION",
  },
];

